import classnames from 'classnames';
import {
  publicUrl,
} from '../../publicUrl';
import {
  Link as ReactRouterLink,
} from 'react-router-dom';

import * as React from 'react';

import styles from './index.module.scss';

export const Link = ({
  children,
  className,
  props,
  to,
}) => (
  <ReactRouterLink
    {...props}
    className={classnames(styles.link, className)}
    to={`${publicUrl}${to ? `${to}` : ''}`}
  >{
    children
  }</ReactRouterLink>
);
